}

.additional-products__title {
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  font-style: normal;
  font-weight: 400;
  width: min-content;
  white-space: nowrap;
  background-color: #ffffff;
  padding: 0 20px;
  margin: 25px 0 37px;
}
.additional-products__title::before {
  content: "";
